import React, {useState, useEffect} from 'react';
import styled from 'styled-components';

const StyledParallaxDiv = styled.div`
  text-align: center;
  position: absolute;
  margin: -100px;
  height: 200px;
  width: 200px;
  left: ${props => (props.offX) * 1}px}
  top: ${props => (props.offY) * 1}px}


  background-color: none;
  border: 3px solid red;
  pointer-events: none;


`
const StyledParallaxDiv2 = styled.div`
  text-align: center;
  position: absolute;
  margin: 700px 500px ;
  height: 250px;
  width: 250px;
  background-color: none;
  border-radius: 50%;
  border: 3px solid blue;
  transform: ${props => `translateY(${-(props.off) * 0.8}px) ` };
  z-index: -4;  
`


export const MyParallax = props => {

  let state={
    x: 0,
    y: 0,
  }
  
  //const [offSet, setOffset] = useState(0)
  const [offSet, setOffset] = useState(state)

  const handleXY = (e) => {
    setOffset({ 
      x : e.pageX,    
      y : e.pageY  
     })
  }
  
  useEffect(() => {

  //   const handleYScroll = () => {
    //   setOffset(window.pageYOffset)
    // }
  




  //   window.addEventListener('scroll', handleYScroll)
     window.addEventListener("mousemove", handleXY)

    return () => {
   //   window.removeEventListener('scroll', handleYScroll)
   window.removeEventListener('mousemove', handleXY)
 

      }
  }, [])


  return (
    <div>
       <StyledParallaxDiv onMouseMove={handleXY} offY={offSet.y} offX={offSet.x} id={props.id}>
         {props.children}
        </StyledParallaxDiv> 

    </div>
   

  )
};
